.container {
    max-width: 320px;
    padding: 0 16px;
    margin: 0 auto;
  
    @media (min-width: 375px) {
      max-width: 480px;
      padding: 0 20px;
    }
  
    @media (min-width: 768px) {
      max-width: 768px;
    }

    @media (min-width: 1280px){
      max-width: 1280px;
    }
  
    @media (min-width: 1920px) {
      max-width: 1920px;
    }
  }